import React, { useEffect } from "react";
import { useHistory } from "react-router-dom";
import "./StartPage.css";
import profile from "../img/cvs.png";
import grid1 from "../img/grid1.jpg";
import grid2 from "../img/grid2.png";
import grid3 from "../img/grid3.jpg";
import grid4 from "../img/grid4.jpg";
import grid5 from "../img/grid5.jpg";
import grid6 from "../img/grid6.jpg";
import grid7 from "../img/grid7.jpg";
import grid8 from "../img/grid8.jpg";
import grid9 from "../img/grid9.jpg";
import grid10 from "../img/grid10.jpg";
import hamburg from "../img/hambur.png";
import diploma from "../img/diploma.png";
import Fade from "react-reveal/Fade";

const StartPage = () => {
  const { push } = useHistory();
  const history = useHistory();

  window.onscroll = function () {
    scrollFunction();
  };

  function linkClick() {
    let width = window.innerWidth;
    if (width < 769) {
      console.log("mindre än" + width);
      var li = document.getElementById("link");
      li.style.width = "27%";
      var lis = document.getElementById("links");
      lis.style.display = "block";
    } else {
      console.log("större ön" + width);
      if (document.getElementById("link") != "null") {
        var li = document.getElementById("link");
        console.log(li.style.width);
        if (li.style.width === "90%") {
          li.style.width = "1vw";
          li.style.height = "1vw";
          var lis = document.getElementById("links");
          lis.style.display = "none";
        } else if (li.style.width === "1vw") {
          li.style.width = "90%";
          var lis = document.getElementById("links");
          lis.style.display = "block";
        }
      }
    }
  }

  function scrollFunction() {
    let width = window.innerWidth;
    if (width < 769) {
      var li = document.getElementById("link");
      li.style.width = "27%";
      var lis = document.getElementById("links");
      lis.style.display = "block";
    } else {
      if (
        document.body.scrollTop > 50 ||
        document.documentElement.scrollTop > 50
      ) {
        var li = document.getElementById("link");
        li.style.width = "1vw";

        li.style.height = "1vw";
        var lis = document.getElementById("links");
        lis.style.display = "none";
        li.onclick = linkClick;
      } else {
        var li = document.getElementById("link");
        li.style.width = "90%";
        var lis = document.getElementById("links");
        lis.style.display = "block";
        li.onclick = linkClick;
      }
    }
  }

  function handleClick() {
    history.push("/Project");
  }

  return (
    <div className="main">
      <div className="content">
        <div id="link">
          <img id="linkimg" source={hamburg}></img>
          <Fade left>
            <div id="links">
              <a onClick="#content">Home</a> |{" "}
              <a href="#contactcontent">Contact</a> |{" "}
              <a href="./Project">Projects</a> |{" "}
              <a href="#divide2">Education</a>
            </div>
          </Fade>
        </div>

        <div className="text">
          <div className="fadeText">
            <Fade left>
              <div className="mainH1">
                <h1>
                  <b>Hi my name is Alice,</b>
                </h1>

                <p> and I am a M.sc. Interaction Technology and Design Graduate</p>
              </div>
            </Fade>
          </div>
        </div>
        {/* /**Kanske tar en hoover med information här istället??? */}
      </div>
      <div className="comp">
        <Fade bottom>
          <div>
            <h1>Projects</h1>
          </div>
        </Fade>

        <Fade bottom>
          <div className="proj">
            <p>
              Projects done over recent time, some done by my self, some with an
              amazing team.
            </p>
            <button onClick={handleClick}>See more!</button>
          </div>
        </Fade>
      </div>

      <div id="divide"></div>
      <div className="aboutMe">
        <Fade left>
          <h1>About ME</h1>
        </Fade>
        <div id="aboutcontent">
          <Fade left>
            <p>
              -Alice born in the late 90's in north parts of Sweden<br />
              -Been studying for 5 years, so almost done with my Masters in
              Interaction Technology and Design.
              <br /> -Logical thinker with a lot of creative energy. Best of
              both worlds I would say. <br />
              -I can code in HTML, css, JavaScript, Java, Python, some C-code.
              <br /> -Design do I preferebly do on paper and in Figma. <br /> -
              Familiar with SQL databases, AI and ML algorithma's.
            </p>
          </Fade>

          <img src={grid8}></img>
        </div>
      </div>

      <div id="divide2"></div>

      <div id="educ">
        <h1>education</h1>
        <div id="educontent">
          <p>
            Currently finishing of my Master in Engineering in Interaction
            Technology and Design at Umeå Univeristy, Sweden. The education is
            primarily focused on services in mobile systems. You become an
            expert in the meeting between man and machine and create technology
            that works, is durable and easy and fun to use.
          </p>
          <p> </p>
          <img src={diploma}></img>
        </div>
      </div>

      <div id="contact">
        <h1>contact</h1>
        <div id="contactcontent">
          <p>
            <b>Contact is easiest via mail:</b>
            <br />
            aliceljungholm@gmail.com.
            <br />
            <br />
            <b>But LinkedIn works too:</b>
            <br />
            <a
              href="https://www.linkedin.com/in/alice-ljungholm-6b780214b"
              target="_blank"
            >
              Alice Ljungholm LinkedIn
            </a>
          </p>
        </div>
      </div>

      <div className="photoGridc1">
        <img src={grid1}></img>
        <img src={grid2}></img>
        <img src={grid3}></img>
        <img src={grid4}></img>
        <img src={grid5}></img>
      </div>
      <div className="photoGridc2">
        <img src={grid6}></img>
        <img src={grid7}></img>
        <img src={grid8}></img>
        <img src={grid9}></img>
        <img src={grid10}></img>
      </div>
      <a
        href="https://www.flaticon.com/free-icons/education"
        title="education icons"
      >
        Education icons created by Freepik - Flaticon
      </a>
    </div>
  );
};
export default StartPage;
